.App{
    background-image:url('../img/backgroundPattern2.png');
    background-size: 50vw;
    background-position: center;
    background-color: rgb(136, 126, 203);
    z-index: 1;
    height: 100%;
}


.header{
    font-family: "commodore_64_angledregular";
    color: rgb(136, 126, 203);
    line-height: 1.2;
    text-align: center;
    border-width: 50px;
    border-color: rgb(136, 126, 203);
    border-style: solid;
    background-color: rgb(80, 69, 155);
  }
  .header h1 {
    font-family: "commodore_64_angledregular";
    color: rgb(136, 126, 203);
    line-height: 1.2;
    text-align: left;
    z-index: 19;
  }
  .header h3 {
    font-family: "commodore_64_angledregular";
    color: rgb(136, 126, 203);
    line-height: 1.2;
    z-index: 19;
  }


  .header h2 {
    font-family: "commodore_64_angledregular";
    color: rgb(136, 126, 203);
    line-height: 1.2;
    text-align: left;
    z-index: 19;
  }


  .main-content{
    margin-top: 5em;
    font-family: 'Lato';
    background-color: rgb(106, 191, 198);
    border-width: 0px;
    border-style: solid;
    background-color:  rgb(80, 69, 155);
    box-shadow: 0px 22px 30.45px 4.55px rgba(161, 104, 60, 0.73);
  }


.custom-carousel{
  height: 20px;
}
.divider{
  font-family: 'commodore_64_angledregular';
  /* background-color: rgb(106, 191, 198); */
  color: white;
  border-width: 0px;
  border-style: solid;
  width: auto;
}
.cardcontainer{
  padding:0;
}


.footer{
  font-family: "commodore_64_angledregular";
  color: rgb(136, 126, 203);
  line-height: 1.2;
  text-align: center;
  border-width: 50px;
  border-color: rgb(136, 126, 203);
  border-style: solid;
  background-color: rgb(80, 69, 155);
  padding-bottom: 1em;
}
.footer a{
  color: rgb(136, 126, 203);
  text-decoration: underline;
  
}

@media only screen and (max-width: 800px) {

  .header{
    border-width: 2vw;
  }
  .header h1{
    font-size: 1em;
  }
  .header h2{
    font-size:1em;
  }
  .header h3{
    font-size:1em;
  }
  .divider{
    font-size:1.5em;
  }
  .footer h1{
    font-size: 1em;
  }
}